@import './reset.scss';

$main-color: #ff0000;

.red {
  color: $main-color;
}

div.container {
  h4 {
    color: blue;
  }
}

@mixin css_alert() {
  background: #eeeeee;
  padding: 15px;
  border-radius: 5px;
  max-width: 500px;
  width: 100%;
  margin: auto;
}

.my-alert {
  @include css_alert();
}
.my-alert p {
  margin-bottom: 0;
}

.my-alert2 {
  @extend .my-alert;
  background: yellow;
}
